.footer__item:not(:last-child) {
  margin: 0px 18px 0px 0px;
}

@media screen and (max-width: 500px) {
  .footer__item {
    padding-bottom: 16px;
  }
}

@media screen and (max-width: 320px) {
  .footer__item {
    padding: 0;
  }
  .footer__item:not(:last-child) {
    margin: 0px 0px 32px;
  }
}
