.cover__container {
  margin: 0px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  max-width: 1184px;
  min-height: 740px;
  background: url(../../../images/cover-trains.jpg) 0 0 no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;
  text-decoration: none;
  color: #ffffff;
  transition: all 0.4s;
}

.cover__container:hover {
  transform: scale(1.1);
}

.cover__container::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: #2a2c2f;
  opacity: 0.3;
  transition: opacity 0.4s;
}

.cover__container:hover::before {
  opacity: 0.8;
}

@media screen and (max-width: 1024px) {
  .cover__container {
    max-width: 1024px;
    min-height: 640px;
  }
}

@media screen and (max-width: 768px) {
  .cover__container {
    max-width: 768px;
    min-height: 480px;
  }
}

@media screen and (max-width: 445px) {
  .cover__container {
    max-width: 445px;
    min-height: 200px;
  }
}

@media screen and (max-width: 320px) {
  .cover__container {
    max-width: 320px;
    min-height: 200px;
  }
}
