.header {
  max-width: 1184px;
  margin: 0 auto;
  padding-top: 28px;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

@media screen and (max-width: 768px) {
  .header {
    margin: 0px 24px;
  }
}

@media screen and (max-width: 320px) {
  .header {
    margin: 0px 16px;
    padding-top: 16px;
  }
}
