.footer__link {
  text-decoration: none;
  color: #ffffff;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.12;
  transition: opacity 0.4s;
}

.footer__link:hover {
  opacity: 0.8;
}

@media screen and (max-width: 320px) {
  .footer__link {
    font-size: 24px;
    line-height: 1;
  }
}
