.lead {
  box-sizing: border-box;
  padding: 56px 0px calc(92px / 2);
  text-align: center;
}

@media screen and (max-width: 1024px) {
  .lead {
    padding: 48px 0px calc(92px / 2);
  }
}

@media screen and (max-width: 764px) {
  .lead {
    padding: 48px 0px calc(64px / 2);
  }
}
