.add__button {
  padding: 0;
  margin: 0 0 15px;
  width: 150px;
  height: 50px;
  background-color: #2a2c2f;
  border: 2px solid #fff;
  border-radius: 5px;
  cursor: pointer;
  background-image: url(../../../images/plus.svg);
  background-position: center;
  background-repeat: no-repeat;
  transition: box-shadow 0.4s;
}

.add__button:hover {
  box-shadow: 0px 0px 10px 3px rgba(109, 155, 191, 0.2) inset;
}

@media screen and (max-width: 768px) {
  .add__button {
    height: 40px;
    width: 100px;
  }
}

@media screen and (max-width: 425px) {
  .add__button {
    height: 35px;
    width: 80px;
  }
}
